import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { RecordingListComponent } from './recording-list/recording-list.component';

const routes: Routes = [
    { path: '', redirectTo: 'recording-list', pathMatch: 'full' },
    { path: 'recording-list', component: RecordingListComponent }
];
@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class RecordingListRoutingModule { }
